.ql-editor {
    font-family: var(--app-text-main-font);
    min-height: 200px;
    overflow: auto;
    border-top: 0px dashed var(--app-color-gray-light);
    border-bottom: 0px dashed var(--app-color-gray-light);
    border-left: 0px dashed var(--app-color-gray-light);
    
}

.ql-picker.ql-font {
    font-family: var(--app-text-main-font);
    font-weight: normal;

    .ql-picker-item {
        font-size: 0;

        &:before {
            content: attr(data-value) !important;
            font-size: 14px;
        }
    }
}
