.mobilepopup {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 0;
  position: fixed;
  top: 200vh;
  background-color: transparent;  
}

.mobilepopupopen {
  position: absolute;
  left: 0;
  top: 0;
  transition: .3s ease-in-out;
  display: flex;
  flex-direction: column;
  width: 100%;  
}

.mobilePopupActionContainer {  
  background-color: rgb(246,244,236);
  //background-color: white;
  padding: 20px 50px 0 50px;
  height: 100vh;
}

.filtersContainer {
  margin-top: 20px;
  width: inherit;
  margin-bottom: 10px;
  border: 1px dashed var(--app-color-gray-light);
  padding: 20px 10px 20px 10px;
  height: inherit;
}

.centerDiv {
  flex-direction: row;
  margin: auto;
  justify-content: center;
  justify-items: center;
  align-content: center;
  align-items: center;
  text-align: center;
}

.sharesTable {
  
}

.fieldInput {
  border: 1px solid var(--app-color-gray-light);
  border-radius: 0px;
  padding-left: 10px;
  align-content: center;
  margin-top: 5px;
  margin-bottom: auto;
  height: 40px;
  font-family: var(--app-text-main-font), serif;
  font-size: 16px;
  font-weight: 700;
  color: var(--app-color-gray-dark);
  width: 100%;
  &::placeholder {
    color: var(--app-color-gray-light);
  }
  background-color: transparent;
}
