.accordionContainer {
  overflow: hidden;
  margin: 0 0 0px 0;
  font-family: var(--app-text-main-font), serif;
  font-size: 18px;
  font-weight: 700;
  background-color: #FFFFFF;
  color: var(--app-color-gray-dark);
  align-items: center;
  border: 1px dashed var(--app-color-gray-light);
  border-radius: 8px;
  outline: none;
  transition: background-color 0.6s ease;
  width: 100%;
}
