.sharesTable {
  //padding: 20px;
}

.itemsTable {

  width: 100%;
  margin: 0 10px;

  table {
    min-width: 100%;
    width: inherit;
    height: inherit;
    font-family: var(--app-text-mobile-font);
    font-size: 14px;
    font-weight: bold;
    text-align: left;
    border-top: 1px dashed #bbb;
    border-left: 1px dashed #bbb;
    border-right: 1px dashed #bbb;

    
    tr {
      :last-child {
        border-right: 0;
      }
    }

    th {
      font-size: 14px;
      color: white;
      background-color: var(--app-color-gray-dark);
      border-top: 1px dashed #bbb;
      border-right: 1px dashed #bbb;
      padding: 10px;
      min-width: none;
      
    }

    td:last-child {
      min-width: 50px;
      
    }

    td {
      font-size: 14px;
      cursor: pointer;
      margin: 0;
      padding: 10px;
      border-bottom: 1px dashed #bbb;
      border-right: 1px dashed #bbb;
      min-width: none;
    }

    tr td {
      height: 50px;
      max-height: 50px;
      min-width: none;
      padding: 0 10px 0 10px;
      margin: 0px;
    }
  }
}

.inputValue {
  outline: none;
  font-family: var(--app-text-main-font);
  font-size: 14px;
  font-weight: normal;
  line-height: 20px;
  color: #4A4A4A;
  text-align: left;
  //width: 100%;
  border: 0px;

  &::placeholder {
    font-family: var(--app-text-mobile-font);
    font-size: 14px;
    text-align: left;
  //  color: var(--app-color-gray-light);

  }

  margin: 0 0 5px 0;
}

.multiselectComponent {
  width: 300px;
  border: 1px solid var(--app-color-gray-lighter);
  border-radius: 4px;
}

.dropdownComponent {
  width: 100%;
  font-size: 14px;
}

.dropdownComponent_ii {
  width: 100%;
  border: 1px dashed var(--app-color-gray-lighter);
  font-size: 20px;

}

.title {
  font-family: var(--app-text-main-font);
  font-size: 14px;
  font-weight: bold;
  text-align: left;
}

.subtitle {
  font-family: var(--app-text-main-font);
  font-size: 12px;
  font-weight: normal;
  text-align: left;
}


.timer {
  font-family: var(--app-text-font-sports);
  color: var(--app-color-gray-dark);
  font-size: 60px;
}

.centerDiv {
  flex-direction: row;
  margin: auto;
  justify-content: center;
  justify-items: center;
  align-content: center;
  align-items: center;
  text-align: center;
}



.avatarUrlImg {
    object-fit: contain;    
    height: 140px;
    width: 140px;
    border-radius: 50%;
    padding: 2px;
}
