.sharesTable {
  //padding: 20px;
}

.itemsTable {
  table {
    width: 100%;
    border-spacing: 0;
    border: 0px solid transparent;
    tr {
      :first-child {
        font-size: 16px;
        font-weight: 700;
      }

      :last-child {
        
      }
    }

    th {
      :first-child {
        font-size: 16px;
        text-align: left;
      }

      text-align: left;
      color: white;
      background-color: var(--app-color-gray-dark);
     
    }

    td {
      text-align: center;
      font-size: 16px;
      border: 0px solid transparent;
      border-bottom: 1px dashed var(--app-color-gray-light);
      padding-bottom: 20px;
      padding-top: 20px;
      vertical-align: top;
    }
  }

  
}

.inputValue {
  outline: none;
  font-family: var(--app-text-main-font);
  font-size: 16px;
  font-weight: normal;
  line-height: 20px;
  color: #4A4A4A;
  text-align: left;
  width: 100%;
  border: 0px;
  &::placeholder{
    font-family: var(--app-text-mobile-font);
    font-size: 16px;
    text-align: left;
    color: var(--app-color-gray-light);  
    
  }
}

.multiselectComponent {
  width: 300px;
  border: 1px solid var(--app-color-gray-lighter);
  border-radius: 4px;  
}

.dropdownComponent {
  width: 100%;
}

.dropdownComponent_ii {
  width: 100%;
  border: 1px dashed var(--app-color-gray-lighter); 
  font-size: 20px;
  
}

.title {
  font-family: var(--app-text-main-font);
  font-size: 16px;
  font-weight: bold;
  text-align: left;
}

.subtitle {
  font-family: var(--app-text-main-font);
  font-size: 12px;
  font-weight: normal;
  text-align: left;
}

.dropdownContainer {
  align-items: center;
  align-self: center;
  align-content: center;
  justify-content: center;
  justify-self: center;
  width: 100%;
  border: 1px solid var(--app-color-gray-lighter);
}

.currencyInput {
  width: 100%;
  border: none;
  font-family: var(--app-text-main-font), serif;
  font-size: 16px;
  font-weight: bold;
  margin-left: 10px;
  color: var(--app-color-gray-dark);
}

.tmiClassName{
  font-weight: bold;
}
.templateNotes {
  border: 1px solid var(--app-color-gray-light);
  width: 100%;
  height: 100px;
  text-align: left;
  font-size: 14px;
  font-family: var(--app-text-main-font);
  font-weight: bold;

}